import FormItem from './FormItem';
import { useState, useRef, useEffect, useContext } from 'react';
import { matchesLabel } from '../../scripts/id-util.js';
import SearchResults from './SearchResults.js';
import { MDBFormContext } from './MDBForm.js';

const FormBlock = (props) => {
    const form = useContext(MDBFormContext);
    const info = form.criticalActions[props.block];
    const [ numGreen, setNumGreen ] = useState(0);
    const [ numYellow, setNumYellow ] = useState(0);
    const [ numRed, setNumRed ] = useState(0);
    const [ numFindings, setNumFindings ] = useState(0);
    const [ numNotes, setNumNotes ] = useState(0);

    const newGreen = () => setNumGreen(numGreen + 1);
    const newYellow = () => setNumYellow(numYellow + 1);
    const newRed = () => setNumRed(numRed + 1);
    const newFindings = () => setNumFindings(numFindings + 1);
    const newNotes = () => setNumNotes(numNotes + 1);

    const lessGreen = () => numGreen > 0 && setNumGreen(numGreen - 1);
    const lessYellow = () => numYellow > 0 && setNumYellow(numYellow - 1);
    const lessRed = () => numRed > 0 && setNumRed(numRed - 1);
    const lessFindings = () => numFindings > 0 && setNumFindings(numFindings - 1);
    const lessNotes = () => numNotes > 0 && setNumNotes(numNotes - 1);

    const deleteSelf = (e) => {
        let block = e.target.parentNode;
        block.remove();
    }

    const [query, setQuery] = useState(["", ""]);
    const [beforeRef, afterRef] = [useRef(), useRef()];
    const setInput = (e) => {
        query[0] === ""
        ? afterRef.current.value = e.target.id
        : beforeRef.current.value = e.target.id;
        setQuery(["", ""]);
    }
    
    useEffect(() => {
        if (form) {
            let info = form.criticalActions[props.block];
            setNumGreen(info.standard.length)
            setNumYellow(info.minimal.length)
            setNumRed(info.contraindicated.length + info.required.length)
            setNumFindings(info.findings.length)
            setNumNotes(info.notes.length)
        }
    }, [form, props.block])
    
    return (
        <div className="mdb-block-wrapper">
            <button type="button" className="delete-self-block" onClick={e => deleteSelf(e)} >-</button>
            <div id="mdb-info" className="mdb-block" >
                <p className="mdb-block-heading" >Info:</p>
                <div className="mdb-info-wrapper">
                    <label>
                        Critical action:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-label`} 
                            autoComplete={"off"} 
                            defaultValue={info.label} 
                        />
                    </label>
                    <label>
                        Protocol:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-protocol`} 
                            autoComplete={"off"} 
                            defaultValue={info.protocol} 
                        />
                    </label>                        
                    <label>
                        Before:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-when-before`} 
                            ref={beforeRef}
                            onChange={(e) => setQuery([e.target.value, ""])}
                            autoComplete={"off"} 
                            defaultValue={info.when.before} 
                        />
                    <SearchResults list={matchesLabel(query[0])} setVal={setInput} />
                    </label>
                    <label>
                        After:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-when-after`} 
                            ref={afterRef}
                            onChange={(e) => setQuery(["", e.target.value])}
                            autoComplete={"off"} 
                            defaultValue={info.when.after} 
                        />
                    <SearchResults list={matchesLabel(query[1])} setVal={setInput} />
                    </label>
                </div>
            </div>

            <div id="mdb-green" className="mdb-block" >
                <p className="mdb-block-heading" >Standard:</p>
                { [...Array(numGreen)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='standard' item={info} edit={lessGreen} />
                )) }

                <div>
                    <button type="button" onClick={newGreen} >Add new</button>
                </div>
            </div>

            <div id="mdb-yellow" className="mdb-block" >
                <p className="mdb-block-heading" >Minimal:</p>
                { [...Array(numYellow)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='minimal' item={info} edit={lessYellow} />
                )) }

                <div>
                    <button type="button" onClick={newYellow} >Add new</button>
                </div>

                <FormItem block={props.block} type='minimalWhy' item={info} />
            </div>

            <div id="mdb-red" className="mdb-block" >
                <p className="mdb-block-heading" >Contraindications:</p>
                { [...Array(numRed)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='contraindicated' item={info} edit={lessRed} />
                )) }

                <div>
                    <button type="button" onClick={newRed} >Add new</button>
                </div>

                <FormItem block={props.block} type='contraindicatedWhy' item={info} />
            </div>

            <div id="mdb-findings" className="mdb-block" >
                <p className="mdb-block-heading" >Findings:</p>
                { [...Array(numFindings)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='findings' item={info} edit={lessFindings} />
                )) }

                <div>
                    <button type="button" onClick={newFindings} >Add new</button>
                </div>
            </div>

            <div id="mdb-notes" className="mdb-block" >
                <p className="mdb-block-heading" >Notes / Comments:</p>
                { [...Array(numNotes)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='notes' item={info} edit={lessNotes} />
                )) }

                <div>
                    <button type="button" onClick={newNotes} >Add new</button>
                </div>
            </div>
        </div>
    );
}

export default FormBlock;