import React from "react"
import { Link } from "react-router-dom"

import Header from './Header'

const HomePage = () => {

  return (
    <>
      <Header />

      <div className="nav-wrapper">
        <Link to="/scenarios">
          <div className="home-nav-button">Scenario Forms</div>
        </Link>
        <Link to="/class-codes-list/">
          <div className="home-nav-button">Class Codes</div>
        </Link>
        <Link to="/debriefer-entry-list/">
          <div className="home-nav-button">Debriefer Entry</div>
        </Link>
      </div>
    </>
  )
}

export default HomePage