import React, { useState, useRef, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import Header from '../Header';
import FormBlock from './FormBlock';
import { verifyDocument, publishDocument, getDocument } from '../../scripts/connect-firebase.js';
import criticalActions from '../../data/blank-crit-actions.json';

const COLLECTION_ID = 'scenario-required-actions';

export const MDBFormContext = React.createContext();

const FormRoot = (props) => {
    const [mdbForm, setMDBForm] = useState(null);
    const [numBlocks, setNumBlocks] = useState(1);
    const { mdbFormId } = props.match.params;
    const formRef = useRef(null);
    const isEdit = () => !!mdbFormId;
    let history = useHistory();

    useEffect(() => {
        mdbFormId ?
        getDocument(COLLECTION_ID, mdbFormId)
            .then(res => {
                setMDBForm(res)
                setNumBlocks(res.criticalActions.length)
            })
            .catch(err => console.error(err))
        : setMDBForm({
            scenarioName: "",
            criticalActions: [
                criticalActions
            ]
        });
    }, [mdbFormId])

    const addBlock = () => {
        mdbForm.criticalActions.push(criticalActions);
        setNumBlocks(numBlocks + 1);
    }

    return (
        <>
        <Header />
        <MDBFormContext.Provider value={mdbForm} >
        <div className="mdb-form-wrapper" >
            {
            mdbForm ? 
            <>
            <form id="debriefer-form" className="debriefer-form" ref={formRef} >
                <label className="mdb-block-heading">
                    Scenario Name:
                    <input type="text" className="mdb-text" id="scenarioName" autoComplete={"off"} defaultValue={mdbForm.scenarioName} />
                </label>

                { [...Array(numBlocks)].map( (e, i) => (
                    <FormBlock key={i} block={i} form={mdbForm} />
                )) }

                <div>
                    <button type="button" onClick={addBlock} >Add new</button>
                </div>
            </form>
            <button type="button" id="submit-bttn" onClick={process}>Submit</button>
            </>
            : <p>Loading...</p>
            }
        </div>
        </MDBFormContext.Provider>
        </>
    );

    async function process() {
        let form = scan(formRef.current, []);
        let object = { scenarioName: null, criticalActions: [] };
        let blocks = {};

        for (let i=0; i < form.length; i++) {
            let item = form[i];
            let path = item.id.split('-');
            // path[0] = block, path[1] = type, path[2] = number

            // Create block if it doesn't exist yet
            if (!blocks[ path[0] ] && path[0] !== "scenarioName") blocks[ path[0] ] = deepCopy(criticalActions);
            
            switch (path.length) 
            {
                default: console.error('Path error.'); break;

                case 1: // Label
                    object[ path[0] ] = item.value; break;

                case 2: // Info
                    blocks[ path[0] ][ path[1] ] = item.value; break;

                case 3: // Actions
                    if (path[1] === 'contraindications')
                    {
                        let type = form[i + 1].checked ? 'required' : 'contraindicated';
                        blocks[ path[0] ][ type ].push(item.value);
                    }
                    else
                    {
                        Array.isArray(blocks[ path[0] ][ path[1] ])
                            ? blocks[ path[0] ][ path[1] ].push(item.value) 
                            : blocks[ path[0] ][ path[1] ] = item.value; 
                    }
                    break;
            }
        }

        // Add to object if it exists or if only 1 block
        for (let block in blocks) {
            if (blocks[block].label || !blocks[1])
                object.criticalActions.push(blocks[block]);
        }

        //Validate & upload object
        if (object.scenarioName.trim() === "" || 
            (!isEdit && 
            await verifyDocument(COLLECTION_ID, object.scenarioName.trim())
        )) {
            alert("You need a new name for the scenario!");
            return;
        } else {
            publishDocument(COLLECTION_ID, object.scenarioName, object);
            alert(`Debriefer info for ${object.scenarioName} uploaded.`);
            history.push(`/associations/${object.scenarioName}`, { formId: object.scenarioName });
        }
    }

    function scan(obj, arr) {
        for (let node of obj) 
            if (node.nodeName === 'INPUT' || node.nodeName === 'TEXTAREA')
                arr.push(node);

        return arr;
    }

    function deepCopy(obj) { return JSON.parse(JSON.stringify(obj)); }
}

export default FormRoot;