const SearchResults = ({ list, setVal }) => {
    const noSelect = { id: "(No selection)", label: "(No selection)" };

    return (
      <>
        {list && (
          <div className={`search-results`}  >
            {list &&
              [noSelect, ...list].map((e, i) => (
                <p
                  key={i}
                  id={e.id}
                  onClick={(e) => setVal(e)}
                  onMouseOver={v => v.target.innerText = e.id}
                  onMouseLeave={v => v.target.innerText = e.label}
                >
                  {e.label}
                </p>
              ))}
          </div>
        )}
      </>
    );
}

export default SearchResults;