import { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { getCollection, deleteDocument } from '../../scripts/connect-firebase.js';
import Header from '../Header';

const COLLECTION_ID = 'class-codes';

const ClassCodesList = () => {
    const [ codes, setCodes ] = useState(null);
    
    useEffect(() => {
        getCollection(COLLECTION_ID)
            .then((result) => { setCodes(result); })
            .catch((err) => { console.log(err); })
    }, []);

    return (
        <>
        <Header />
        <div className="scenario-assignment-list">
            <h1>Class Codes</h1>
            <Link to={`/class-codes/`} className="new-assignment-bttn scenario-assignment-list-item">
                <p>Create new class code +</p>
            </Link>
            {
                codes &&
                codes.map( item => (
                    <ListItem item={item} key={item.title} remove={handleDelete} />
                ))
            }
        </div>
        </>
    );

    async function handleDelete(item) {
        let id = item.title;    
        if( window.confirm('Are you sure you want to permanetly delete this group? This cannot be undone!') ){
            deleteDocument(COLLECTION_ID, id);
            let newGroups = codes.slice();
            newGroups.splice( newGroups.indexOf(item), 1);

            setCodes(newGroups);
        } else
            console.log(`Delete of ${id} cancelled.`);
    }
};

const ListItem = ({ item, remove }) => {
    return (
        <div className="scenario-assignment-list-item">
            <p>{item.title}</p>

            <Link to={`/class-codes/${item.title}`} className="action-bttn" >Edit</Link>
            <button type="button" className="action-bttn" onClick={() => remove(item)}>Delete</button>
        </div>
    );
}

export default ClassCodesList;