import ScenarioItem from "./ScenarioItem.js";
import scenarioData from "../../data/trauma-scenario.json";
import classnames from 'classnames';

const Steplist = ({ active, scenario }) => {
	return (
		<div id="scenario-steps" className={classnames({"scenario-view-hidden": !active})}>
			<h1>Scenario Items</h1>
			{getSteplistItems(scenarioData.steps, []).map((item) => (
				<ScenarioItem key={item.id} item={item} scenario={scenario} />
			))}
		</div>
	);
};

// Recursively get the nested assessment items
function getSteplistItems(item, array) {
	for (let step of item) {
		if (step.type === 'heading' && step.children != null) {
			let assessCount = 0;
			for (let item of step.children) 
				if (item.type === 'assess') 
					assessCount += 1;
			
			if (assessCount > 0 )
				array.push({ 'id': step.id, 'type': step.type, 'label': step.label });

			getSteplistItems(step.children, array);
		} else if (step.type === 'assess') {
			array.push({ 'id': step.id, 'type': step.type, 'label': step.label });

			if (step.children != null)
				getSteplistItems(step.children, array);
		}
	}

	return array;
}

export default Steplist;
