import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { getCollection } from '../../scripts/connect-firebase.js';
import Header from '../Header.js';

const COLLECTION_ID = 'scenario-required-actions';
const MDBFormList = () => {
    const [formsList, setFormsList] = useState(null);

    useEffect(() => {
        getCollection(COLLECTION_ID)
            .then(res => setFormsList(res))
            .catch(err => console.error(err));
    }, []);

    return (
        <>
        <Header />
        <div id="mdb-form-list-wrapper">
            <h1>MDB Debriefer Forms</h1>
            <Link to={`/debriefer-entry/`} className="mdb-form-list-item" id="new-mdb-form" >Create new</Link>
            {
                formsList ?
                formsList.map((ele, i) => (
                    <MDBFormListItem item={ele} key={i} />
                ))
                : <p>Loading...</p>
            }
        </div>
        </>
    );
};

const MDBFormListItem = ({item}) => {
    return (
        <div className="mdb-form-list-item">
            <>
            <h3>{item.scenarioName}</h3>
            <Link to={`/debriefer-entry/${item.scenarioName}`} className="action-bttn" >Edit</Link> 
            </>
        </div>
    );
}

export default MDBFormList;