import classnames from 'classnames';

const Vitals = ({ active, scenario }) => {
	return (
		<div className={classnames("info-box vitals", {"scenario-view-hidden": !active})}>
			<VitalsBox type="initial" header="Initial" scenario={scenario} />
			<VitalsBox type="good" header="Good Reassessment" scenario={scenario} />
			<VitalsBox type="bad" header="Bad Reassessment" scenario={scenario} />
		</div>
	);
};

const VitalsBox = ({ scenario, header, type }) => {
	return (
		<div className="vital-wrapper">
			<h1> {`${header} Vitals:`} </h1>
			<div className="vitals-box">
				<Vital label='BP' type={type} scenario={scenario} />
				<Vital label='P' type={type} scenario={scenario} />
				<Vital label='R' type={type} scenario={scenario} />
				<Vital label='Skin' type={type} scenario={scenario} />
				<Vital label='Spo2' type={type} scenario={scenario} />
				<Vital label='ETCO2' type={type} scenario={scenario} />
				<Vital label='GCS' type={type} scenario={scenario} />
				<Vital label='Glucose' type={type} scenario={scenario} />
				<Vital label='Pain' type={type} scenario={scenario} />
				<Vital label='Temp' type={type} scenario={scenario} />
			</div>
		</div>
	);
};

function Vital ({scenario, label, type}) {
	return (
		<label>
			{label}:
			<input
				id={`vitalSigns-${label}-${type}`}
				type="text"
				defaultValue={scenario.vitalSigns[label][type]}
			/>
		</label>
	);
}

export default Vitals;
