import { getCollection, 
        getDocument, 
        publishDocument,
        verifyDocument,
        deleteDocument } 
from '../../scripts/connect-firebase.js';
import { useHistory } from "react-router-dom";
import { useState, useEffect, useRef } from 'react';
import Header from '../Header';

const COLLECTION_ID = 'class-codes';

const ClassCode = (props) => {
    const { classCode } = props.match.params;
    const [ scenarios, setScenarios ] = useState(null);
    const [ assignment, setAssignment ] = useState(null);
    const [ originalId, isEdit ] = [ classCode, !!classCode ];
    const assignmentTitle = useRef(null);
    const checklist = useRef(null);
    const orderRequired = useRef(null);
	let history = useHistory();
    
    useEffect(() => {
        getCollection('scenarios')
            .then((result) => { 
                setScenarios(result); // Load scenarios from db

                classCode ? // Load assignment if an edit
                getDocument(COLLECTION_ID, classCode)
                    .then((result) => { 
                        setAssignment(result);
                    })
                    .catch((err) => console.log(err)) 
                : setAssignment([]);

            })
            .catch(() => console.log('Scenario loaded error') )
    }, [classCode]);

    const processAssignment = () => {
        let form = checklist.current.children;
        let assignmentObj = [];

        // Get assignments and place in object
        for(let i=0; i < form.length; i++){
            if(form[i].children[0].checked)
                assignmentObj.push({ // New objects
                    id: form[i].children[0].value,
                    order: parseInt(form[i].children[1].children[0].value) || (orderRequired.current.checked ? 1: -1)
                });
        }
        
        return assignmentObj;
    }

    const handleSubmit = async () => {
        let title = assignmentTitle.current.value;
        
        let assignmentTasks = processAssignment();

        console.log(assignmentTasks);
        
        // Verify name isn't empty and doesn't already exists (prevent overwriting)
        if ( title.trim() === '' || (await verifyDocument(COLLECTION_ID, title) && !isEdit) ) {
            alert('Assignment needs a title!');
            return;
        }

        (isEdit && originalId !== title) && deleteDocument(COLLECTION_ID, originalId);

        await publishDocument( COLLECTION_ID, title, 
            { title: title, orderRequired: orderRequired.current.checked, assignments: assignmentTasks });

        alert(`Published ${assignmentTitle.current.value}`);
        history.push('/class-codes-list/');
    };

    const isChecked = (id) => {
        let checked = false;
        assignment?.assignments?.forEach(item => {
            if(item.id === id) checked = true;
        });
        return checked;
    };

    const orderNum = (id) => {
        let num = null;
        assignment?.assignments?.forEach(item => {
            if(item.id === id) num = item.order;
        });
        return num;
    };

    return (
        <>
        <Header />
        <div className="scenario-assignment">
            <h1>Class Code</h1>
            {
                assignment ? // Check if assignment loaded in
                <>
                <label className="assignment-title">
                    Title: <input type="text" ref={assignmentTitle} defaultValue={classCode} /> 
                </label>

                <div className="assignment-list" ref={checklist}>
                    {
                        scenarios.map( (item) => (
                            <label key={item.name} className="assignment-check-item">
                                <input type="checkbox" value={item.name} defaultChecked={isChecked(item.name)}/>
                                {item.name}

                                <span>
                                    # <input type="number" defaultValue={orderNum(item.name)}/>
                                </span>
                            </label>
                        ))
                    }
                </div>

                <label className="required-bttn">
                    Order required: <input type="checkbox" ref={orderRequired} defaultChecked={assignment.orderRequired} />
                </label>
                </>
                : <p>Loading...</p>
            }
            <button type="button" onClick={() => handleSubmit()}>Submit</button>
        </div>
        </>
    );
};

export default ClassCode;