import { useState, useRef } from 'react';
import { matchesAny } from '../../scripts/id-util.js';
import SearchResults from './SearchResults.js';

const FormItem = (props) => {
    const [query, setQuery] = useState("");
    const textRef = useRef("");

	const deleteSelf = (e) => {
		// Handle wrapper of 'contraindicated'
		let item = props.type !== 'contraindicated'
		 	? e.target.parentNode
			: e.target.parentNode.parentNode;
		item.remove();
	}

    const setVal = (e) => {
        textRef.current.value = e.target.id;
        setQuery("");
    }

	const getPos = (num) => {
		if (!props.item.meta) return ["", 0];
		let str = props.item.meta.contraReqOrder[num];
		let pos = props.item.contraindicated.indexOf(str);
		if (pos < 0)
			pos = props.item.required.indexOf(str);
		return [str, pos];
	}

	const getChecked = (type) => {
		const [str, num] = getPos(props.num);
		return props.item[type][num] === str;
	}

    switch (props.type) {
		case 'minimalWhy':
        case 'contraindicatedWhy':
			return (
				<label className="mdb-block-item reasoning-block">
					<textarea
						rows="2"
						type="text"
						className="mdb-textarea no-margin"
						id={`${props.block}-${props.type}`}
						autoComplete={"off"}
						defaultValue={props.item[props.type]}
						/>
				</label>
			  );

        case 'notes':
            return (
				<label className="mdb-block-item">
					<input
					type="text"
					className="mdb-text no-left-margin"
					id={`${props.block}-${props.type}-${props.num}`}
					autoComplete={"off"}
					defaultValue={props.item[props.type]}
					/>
					<button type="button" onClick={e => deleteSelf(e)} >-</button>
				</label>
            );

        case 'contraindicated':
            return (
				<div className="mdb-red-item">
					<label className="mdb-block-item">
					ID:
					<input
						type="text"
						id={`${props.block}-contraindications-${props.num}`}
						className="mdb-text"
						onChange={(e) => setQuery(e.target.value)}
						ref={textRef}
						autoComplete={"off"}
						defaultValue={props.item.meta.contraReqOrder[props.num]}
					/>
					<SearchResults list={matchesAny(query)} setVal={setVal} />
					<button type="button" onClick={e => deleteSelf(e)} >-</button>					
					</label>

					<div className="mdb-red-type">
						<label className="red-type">
							Required
							<input
							type="radio"
							id={`${props.block}-required-${props.num}`}
							name={`${props.block}-red-type-${props.num}`}
							value="required"
							defaultChecked={getChecked('required')}
							/>
						</label>

						<label name="red-type" className="red-type">
							Contraindicated
							<input
							type="radio"
							id={`${props.block}-contraindicated-${props.num}`}
							name={`${props.block}-red-type-${props.num}`}
							value="contraindicated"
							defaultChecked={getChecked('contraindicated')}
							/>
						</label>
					</div>
				</div>
            );

        default:
            return (
				<label className="mdb-block-item">
					ID:
					<input
					type="text"
					id={`${props.block}-${props.type}-${props.num}`}
					className="mdb-text"
					onChange={(e) => setQuery(e.target.value)}
					ref={textRef}
					autoComplete={"off"}
					defaultValue={props.item[props.type][props.num]}
					/>
				<SearchResults list={matchesAny(query)} setVal={setVal} />
				<button type="button" onClick={e => deleteSelf(e)} >-</button>
				</label>
            );
    }
}

export default FormItem;